<!-- src/components/FavoriteItem.vue -->
<template>
  <ViewItem
    :title="item.name"
    :subtitle="item.description"
    :imageUrl="getImageUrl(item.image)"
    :currentView="currentView"
    :placeholderIcon="getCategoryIcon(category)"
    @click="$emit('toggleShowcase', item, category)"
  >
    <template #compact-content>
      <p class="item-title">{{ item.name }}</p>
    </template>

    
    <template #list-content>
      <h3 class="item-title">{{ item.name }}</h3>
      <div v-if="item.description" class="item-subtitle">{{ truncateText(item.description, 100) }}</div>
    </template>

    <template #grid-content>
      <p class="grid-item-title">{{ truncateText(item.name, 30) }}</p>
    </template>
  </ViewItem>
</template>

<script>
import ViewItem from '@/components/ViewItem.vue'

export default {
  name: 'FavoriteItem',
  components: { ViewItem },
  props: {
    item: {
      type: Object,
      required: true
    },
    category: {
      type: String,
      required: true
    },
    currentView: {
      type: String,
      default: 'list'
    }
  },
  methods: {
    getImageUrl(image) {
      return image ? `${process.env.BASE_URL}assets/favorites/${image}` : null;
    },
    getCategoryIcon(category) {
      const iconMap = {
        artists: 'palette',
        movies: 'film',
        books: 'book',
        // ... add other categories as needed
      };
      return iconMap[category] || 'star';
    },
    truncateText(text, maxLength) {
      if (!text) return '';
      return text.length <= maxLength ? text : text.slice(0, maxLength - 3) + '...';
    }
  }
};
</script>
