<!-- src/components/ActivityItem.vue -->
<template>
  <ViewItem
    :title="activity.name"
    :subtitle="getActivitySubtitle()"
    :imageUrl="getImageUrl(activity.image)"
    :currentView="currentView"
    :progress="getActivityProgress()"
    :placeholderIcon="getActivityIcon(activity.type)"
  >
    <template #compact-content>
      <p class="item-title">{{ activity.name }}</p>
    </template>
    
    <template #list-content>
      <h3 class="item-title">{{ activity.name }}</h3>
      <div v-for="(modality, index) in activity.modalities" :key="index" class="modality-container">
        <h4 class="modality-name">{{ modality.name }}</h4>
        <p class="modality-progress">
          Progress: {{ modality.current }}/{{ modality.target }} {{ modality.unit }}
          <span v-if="modality.weight">
            (Weight: {{ modality.weight.current }}/{{ modality.weight.target }} kg)
          </span>
        </p>
      </div>
    </template>
  </ViewItem>
</template>

<script>
import ViewItem from '@/components/ViewItem.vue'

export default {
  name: 'ActivityItem',
  components: { ViewItem },
  props: {
    activity: {
      type: Object,
      required: true
    },
    currentView: {
      type: String,
      default: 'list'
    }
  },
  computed: {
    showProgress() {
      return this.activity.modalities && this.activity.modalities.length > 0;
    },
    progressPercentage() {
      if (!this.showProgress) return 0;
      const modality = this.activity.modalities[0];
      return Math.round((modality.current / modality.target) * 100);
    }
  },
  methods: {
    getImageUrl(imageName) {
      return imageName ? `${process.env.BASE_URL}assets/physical/${imageName}` : null;
    },
    getActivityIcon(type) {
      const iconMap = {
        pushup: 'dumbbell',
        pullup: 'dumbbell',
        plank: 'dumbbell',
        muscleup: 'dumbbell',
        // Add more mappings as needed
      };
      return iconMap[type] || 'question';
    },
    getActivitySubtitle() {
      return this.activity.modalities && this.activity.modalities.length > 0
        ? `${this.activity.modalities[0].current}/${this.activity.modalities[0].target} ${this.activity.modalities[0].unit}`
        : '';
    },
    getActivityProgress() {
      return this.progressPercentage;
    }
  }
}
</script>

<style scoped>
.modality-container {
  margin-top: var(--spacing-small);
}

.modality-name {
  font-weight: bold;
  margin-bottom: var(--spacing-x-small);
}

.modality-progress {
  font-size: var(--font-size-small);
}
</style>
