<!-- src/components/ViewItem.vue -->
<template>
  <div class="item-card" :class="currentView">
    <template v-if="currentView === 'compact'">
      <div class="item-content">
        <slot name="compact-content">
          <p class="item-title">{{ title }}</p>
        </slot>
      </div>
    </template>
    
    <template v-else-if="currentView === 'list'">
      <div class="item-image-container">
        <slot name="item-image">
          <img v-if="imageUrl" :src="imageUrl" :alt="title + ' image'" class="item-image">
          <div v-else class="item-image-placeholder">
            <font-awesome-icon :icon="placeholderIcon" />
          </div>
        </slot>
      </div>
      <div class="item-details">
        <slot name="list-content">
          <h3 class="item-title">{{ title }}</h3>
          <div v-if="subtitle" class="item-subtitle">{{ subtitle }}</div>
        </slot>
      </div>
    </template>

 <template v-else-if="currentView === 'grid'">
      <div class="grid-item-container">
        <div class="image-wrapper">
          <div v-if="progress !== undefined" class="progress-indicator">
            {{ progress }}%
          </div>
          <slot name="grid-image">
            <img v-if="imageUrl" :src="imageUrl" :alt="title + ' image'" class="grid-item-image">
            <div v-else class="grid-item-image-placeholder">
              <font-awesome-icon :icon="placeholderIcon" />
            </div>
          </slot>
        </div>
        <div class="grid-item-content">
          <h3 class="grid-item-title">{{ truncateText(title, 30) }}</h3>
          <p v-if="subtitle" class="grid-item-subtitle">{{ truncateText(subtitle, 30) }}</p>
        </div>
      </div>
    </template>

  </div>
</template>


<script>
export default {
  name: 'ViewItem',
  props: {
    title: {
      type: String,
      required: true
    },
    subtitle: {
      type: String,
      default: ''
    },
    imageUrl: {
      type: String,
      default: ''
    },
    currentView: {
      type: String,
      default: 'list'
    },
    placeholderIcon: {
      type: [String, Array],
      default: 'star'
    },
    progress: {
      type: Number,
      default: undefined
    }
  },
  methods: {
    truncateText(text, maxLength) {
      if (!text) return '';
      return text.length <= maxLength ? text : text.slice(0, maxLength - 3) + '...';
    }
  }
}
</script>

