<!-- src/components/BlogItem.vue -->
<template>
  <ViewItem
    :title="post.title"
    :subtitle="formatDate(post.date)"
    :imageUrl="post.image"
    :currentView="currentView"
    :progress="post.progress"
    placeholderIcon="pen"
  >
    <template #compact-content>
      <p class="item-title">{{ post.title }}</p>
    </template>
    
    <template #list-content>
      <h3 class="item-title">{{ post.title }}</h3>
      <div class="item-subtitle">{{ formatDate(post.date) }}</div>
      <p v-if="post.excerpt" class="item-excerpt">{{ post.excerpt }}</p>
    </template>

    <template #grid-content>
      <p class="grid-item-title">{{ truncateTitle(post.title) }}</p>
      <p class="grid-item-subtitle">{{ formatDate(post.date) }}</p>
    </template>
  </ViewItem>
</template>

<script>
import ViewItem from '@/components/ViewItem.vue'

export default {
  name: 'BlogItem',
  components: { ViewItem },
  props: {
    post: {
      type: Object,
      required: true
    },
    currentView: {
      type: String,
      default: 'compact'
    }
  },
  methods: {
    formatDate(dateString) {
      if (!dateString) return 'Not available'
      const options = { year: 'numeric', month: 'long', day: 'numeric' }
      return new Date(dateString).toLocaleDateString(undefined, options)
    },
    truncateTitle(title) {
      if (!title) return ''
      return title.length > 40 ? title.substring(0, 37) + '...' : title
    }
  }
}
</script>

<style scoped>
.item-excerpt {
  font-size: var(--font-size-small);
  margin-top: var(--spacing-small);
}
</style>
