<!-- src/components/BookItem.vue -->
<template>
<ViewItem
  :title="book.displayName || book.title"
  :subtitle="book.author"
  :imageUrl="book.image"
  :currentView="currentView"
  :progress="book.progress"
  placeholderIcon="book"
>

    <template #compact-content>
      <p class="item-title">{{ book.displayName || book.title }}</p>
    </template>

<template #list-content>
      <div class="book-content">
        <h3 class="book-title">{{ book.displayName || book.title }}</h3>
        <div class="item-details">
          <div v-if="book.author" class="item-info">
            <font-awesome-icon :icon="['fas', 'user']" class="info-icon" />
            <span>{{ book.author }}</span>
          </div>
          <div v-if="book.artist" class="item-info">
            <font-awesome-icon :icon="['fas', 'paint-brush']" class="info-icon" />
            <span>{{ book.artist }}</span>
          </div>
          <div v-if="book.dateRead" class="item-info">
            <font-awesome-icon :icon="['fas', 'calendar-alt']" class="info-icon" />
            <span>{{ formatDate(book.dateRead) }}</span>
          </div>
        </div>
      </div>
      <div v-if="book.progress !== undefined" class="progress-container">
        <font-awesome-icon :icon="['fas', 'clock']" class="info-icon" />
        <div class="progress-bar">
          <div class="progress" :style="progressStyle"></div>
        </div>
        <span class="progress-text">{{ book.progress }}%</span>
      </div>
    </template>



    <template #grid-content>
      <img v-if="book.image" :src="book.image" :alt="book.displayName + ' cover'" class="grid-item-image">
      <div v-else class="grid-item-image-placeholder">
        <font-awesome-icon :icon="['fas', 'book']" />
      </div>
      <p class="grid-item-title">{{ truncateText(book.displayName || book.title, 30) }}</p>
      <p class="grid-item-author">{{ truncateText(book.author, 30) }}</p>
    </template>
  </ViewItem>
</template>

<script>
import { ref, computed, onMounted, watch } from 'vue';
import ViewItem from '@/components/ViewItem.vue';

export default {
  name: 'BookItem',
  components: { ViewItem },
  props: {
    book: {
      type: Object,
      required: true
    },
    currentView: {
      type: String,
      default: 'list'
    }
  },
  setup(props) {
    const animationTriggered = ref(false);
    
    const progressStyle = computed(() => ({
      width: animationTriggered.value ? `${props.book.progress}%` : '0%',
      transition: 'width 1s ease-out'
    }));

    const triggerAnimation = () => {
      animationTriggered.value = false;
      setTimeout(() => {
        animationTriggered.value = true;
      }, 50);
    };

    onMounted(triggerAnimation);

    watch(() => props.currentView, triggerAnimation);

    const formatDate = (dateString) => {
      if (!dateString) return 'Not available';
      const options = { year: 'numeric', month: 'long', day: 'numeric' };
      return new Date(dateString).toLocaleDateString(undefined, options);
    };

    const truncateText = (text, maxLength) => {
      if (!text) return '';
      return text.length <= maxLength ? text : text.slice(0, maxLength - 3) + '...';
    };

    return {
      progressStyle,
      formatDate,
      truncateText
    };
  }
}
</script>


