  display: flex;
  align-items: center;
  gap: var(--spacing-small);
  padding-top: var(--spacing-small);
  position: absolute;
  bottom: var(--spacing-small);
  left: calc(100px + var(--spacing-medium)); /* Adjust based on image width and margin */
  right: var(--spacing-small);
}

.progress-bar {
  flex-grow: 1;
  height: 5px;
  background-color: var(--progress-bar-background);
